/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

* {
  box-sizing: border-box;
}

p {
  display: block;
  margin-block-start: 1em;
  margin-block-end: 1em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

body {
  font-family: priori-sans, sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 15px;
  line-height: 1.4;
}

h1, h2, h3, h4, h5, h6 {
  font-family: questa-grande, serif;
  font-weight: 400;
  font-style: normal;
  color: rgb(2, 13, 26);
}

h1 {
  font-size: 50px;
}

h2 {
  font-size: 20px;
  padding: 5px;
  border-top: 1px solid rgb(2, 179, 179);
  border-bottom: 2px solid rgb(2, 179, 179);
  margin-bottom: 20px;
}

button {
  border: none;
  background: #02C2AD;
  color: #FFFFFF;
  font-family: priori-sans, sans-serif;
  font-weight: 700;
  font-style: normal;
  text-transform: uppercase;
  font-size: 15px;
  line-height: 1.5;
  padding: 5px 20px;
  margin-right: 15px;
  cursor: pointer;
}
button.edit-toggle {
  position: absolute;
  top: 0;
  right: 0;
}

#App {
  width: 1000px;
  margin: 0 auto;
  padding: 30px;
  background-image: url('../imgs/background.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50%;
}

#Instructions {
  height: 100vh;
  width: 100%;
  position: fixed;
  z-index: 100;
  background-color: rgb(2, 13, 26);
  top: 0;
  left: 0;
  color: #FFFFFF;
  font-size: 18px;
  display: grid;
  justify-content: center;
  align-content: center;
}
#Instructions .container {
  background-color: rgb(2, 13, 26);
  width: 600px;
  padding: 50px;
}
#Instructions h2 {
  color: #FFFFFF;
  border-color: #FFFFFF;
}
#Instructions ul {
  margin-bottom: 20px;
}
#Instructions ul li {
  list-style: demical;
  margin-left: 20px;
}
#Instructions button {
  background-color: #FFFFFF;
  color: rgb(2, 179, 179);
}

#Resume {
  display: grid;
  grid-template-columns: 200px auto;
}
#Resume > * {
  background-color: rgba(250, 249, 247, 0.9);
}
#Resume #Aside {
  background-color: rgb(2, 13, 36);
  color: #FFFFFF;
  padding: 30px;
  display: grid;
  align-content: space-between;
  width: 200px;
  position: relative;
}
#Resume #Aside #Info {
  text-align: right;
  width: 140px;
  margin-bottom: 100px;
  position: relative;
}
#Resume #Aside #Info .header {
  background: #C5BFEF;
  padding: 10px 10px 10px 60px;
  position: relative;
  top: -300px;
  left: -60px;
  width: 230px;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 0.5em;
  text-align: left;
}
#Resume #Aside #Info #profile-photo {
  height: 250px;
  width: 250px;
  background-size: cover;
  background-position: 50%;
  border-radius: 50%;
  position: relative;
  top: -40px;
  z-index: 1;
}
#Resume #Aside #Info #profile-photo:hover:before {
  content: 'edit';
  height: 100%;
  width: 100%;
  display: block;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  color: #000000;
  font-size: 30px;
  text-align: center;
  padding-top: 100px;
  box-sizing: border-box;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
}
#Resume #Aside #Info h2 {
  border: none;
  padding: 0;
  color: #FFFFFF;
  font-size: 15px;
  margin: 0;
  font-family: priori-sans, sans-serif;
  font-weight: 700;
  font-style: normal;
  text-transform: uppercase;
}
#Resume #Aside #Info .address, #Resume #Aside #Info .phone, #Resume #Aside #Info .email {
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  padding-bottom: 5px;
  margin-bottom: 5px;
}
#Resume #Aside #info .edit-form {
  top: 300px;
  left: 0;
  text-align: left;
}
#Resume #Aside #Info .edit-toggle {
  display: none;
  top: 300px;
  left: 0;
}
#Resume #Aside #Info:hover .edit-toggle {
  display: block;
}
#Resume #Aside #Skills {
  text-align: center;
  position: relative;
}
#Resume #Aside #Skills h2 {
  color: #FFF;
  border-color: #FFF;
}
#Resume #Aside #Skills .edit-form {
  top: 60px;
  left: 0;
  text-align: left;
}
#Resume #Aside #Skills .edit-toggle {
  display: none;
  top: 60px;
  left: 0;
}
#Resume #Aside #Skills:hover .edit-toggle {
  display: block;
}
#Resume main {
  padding: 30px;
  position: relative;
}
#Resume main #Intro {
  padding-left: 100px;
  min-height: 300px;
  position: relative;
}
#Resume main #Intro .title {
  font-size: 20px;
  text-transform: uppercase;
  margin-bottom: 20px;
}
#Resume main #Intro h2 {
  border: none;
  padding: 0;
  margin-bottom: 5px;
}
#Resume main #Intro .profile {
  padding-bottom: 30px;
}
#Resume main #Intro .edit-form {
  top: 0;
}
#Resume main #Intro .edit-toggle {
  display: none;
}
#Resume main #Intro:hover .edit-toggle {
  display: block;
}
#Resume main #Education {
  position: relative;
  min-height: 100px;
}
#Resume main #Education > div {
  display: grid;
  grid-template-columns: 200px auto;
  grid-column-gap: 40px;
  margin-bottom: 50px;
}
#Resume main #Education > div:not(:last-child) {
  padding-bottom: 20px;
  margin-bottom: 20px;
}
#Resume main #Education .education-date {
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin-bottom: 5px;
  text-align: right;
}
#Resume main #Experience .education-degree {
  font-weight: 700;
  color: rgb(2, 179, 179);
  font-size: 18px;
  text-align: right;
}
#Resume main #Education .education-school {
  font-style: italic;
  font-weight: 700;
  font-size: 20px;
}
#Resume main #Education .education-location {
  color: #666666;
  font-style: italic;
}
#Resume main #Education .edit-toggle {
  display: none;
  top: 60px;
}
#Resume main #Education:hover .edit-toggle {
  display: block;
}
#Resume main #Experience {
  position: relative;
}
#Resume main #Experience > div {
  display: grid;
  grid-template-columns: 200px auto;
  grid-column-gap: 40px;
}
#Resume main #Experience > div:not(:last-child) {
  padding-bottom: 20px;
  margin-bottom: 20px;
}
#Resume main #Experience .experience-date {
  text-transform: uppercase;
  letter-spacing: 0.1em;
  margin-bottom: 5px;
  text-align: right;
}
#Resume main #Experience .experience-title {
  font-weight: 700;
  color: rgb(2, 179, 179);
  font-size: 18px;
  text-align: right;
}
#Resume main #Experience .experience-company {
  font-style: italic;
  font-weight: 700;
  font-size: 20px;
}
#Resume main #Experience .experience-location {
  color: #666666;
  font-style: italic;
}
#Resume main #Experience .edit-toggle {
  display: none;
  top: 60px;
}
#Resume main #Experience:hover .edit-toggle {
  display: block;
}

.edit-form {
  position: absolute;
  top: 60px;
  right: 0;
  background-color: rgb(2, 179, 179);
  color: #FFFFFF;
  padding: 20px;
  width: 300px;
  z-index: 20;
}
.edit-form input, .edit-form textarea {
  width: 100%;
  border: 1px solid rgba(255, 255, 255, 0.5);
  background: none;
  border-radius: 0;
  margin-bottom: 10px;
  font-family: priori-sans, sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 15px;
  line-height: 1.5;
  padding: 5px 10px;
  color: #FFFFFF;
}
.edit-form button {
  background-color: #FFFFFF;
  color: rgb(2, 179, 179);
}